body {
  margin: 0;
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
  font-family: 'Segoe UI', sans-serif;
}

.header {
  text-align: center;
  padding: 30px 20px;
}

.pfp {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.animated-name span {
  display: inline-block;
  opacity: 0;
  position: relative;
  animation: dropIn 1s forwards;
}

.navbar {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 40px;
  padding: 0;
  margin-top: 20px;
}

.navbar li a {
  text-decoration: none;
  font-weight: 600;
  color: #333;
  position: relative;
  transition: color 0.3s;
}

.navbar li a::after {
  content: '';
  position: absolute;
  left: 0; bottom: -5px;
  width: 100%;
  height: 2px;
  background: #007acc;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}

.navbar li a:hover {
  color: #007acc;
}

.navbar li a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}

@keyframes dropIn {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes shakeFlip {
  0% { transform: rotateY(0) rotateZ(0); }
  25% { transform: rotateY(10deg) rotateZ(2deg); }
  50% { transform: rotateY(-10deg) rotateZ(-2deg); }
  75% { transform: rotateY(10deg) rotateZ(2deg); }
  100% { transform: rotateY(0) rotateZ(0); }
}

.colored {
  animation: colorChange 10s infinite alternate;
}

@keyframes colorChange {
  0% { color: #333; }
  25% { color: #007acc; }
  50% { color: #e91e63; }
  75% { color: #00bcd4; }
  100% { color: #333; }
}
